	module.exports = {"data":{"challengeNode":{"title":"Animate Elements at Variable Rates","description":["改变相似元素的动画频率的方法有很多。目前接触到的就有<code>animation-iteration-count</code>和<code>@keyframes</code>。","举例说明，右边的动画包含了两个小星星，每个小星星都在 20%<code>@keyframes</code>处变小并且 opacity 变为 20%，也就是一闪一闪的动画效果。你可以通过改变其中一个星星的<code>@keyframes</code>规则以使两个小星星以不同的频率闪烁。","<hr>","通过改变 class 为<code>star-1</code>的元素的<code>@keyframes</code>为 50% 以改变其动画频率。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/applied-visual-design/animate-elements-at-variable-rates","blockName":"Applied Visual Design","tests":[{"text":"<code>star-1</code>class 的<code>@keyframes</code>规则应该为50%。","testString":"assert(code.match(/twinkle-1\\s*?{\\s*?50%/g), '<code>star-1</code>class 的<code>@keyframes</code>规则应该为50%。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  .stars {\n    background-color: white;\n    height: 30px;\n    width: 30px;\n    border-radius: 50%;\n    animation-iteration-count: infinite;\n  }\n\n  .star-1 {\n    margin-top: 15%; \n    margin-left: 60%;\n    animation-name: twinkle-1;\n    animation-duration: 1s;\n  }\n\n  .star-2 {\n    margin-top: 25%;\n    margin-left: 25%;\n    animation-name: twinkle-2;\n    animation-duration: 1s;\n  }\n\n  @keyframes twinkle-1 {\n    20% {\n      transform: scale(0.5);\n      opacity: 0.5;\n    }\n  }\n\n  @keyframes twinkle-2 {\n    20% {\n      transform: scale(0.5);\n      opacity: 0.5;\n    }\n  }\n\n  #back {\n    position: fixed;\n    padding: 0;\n    margin: 0;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background: linear-gradient(black, #000099, #66c2ff, #ffcccc, #ffeee6);\n  }\n</style>\n\n<div id=\"back\"></div>\n<div class=\"star-1 stars\"></div>\n<div class=\"star-2 stars\"></div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/applied-visual-design/animate-multiple-elements-at-variable-rates","id":"587d78a8367417b2b2512ae5"},"slug":"/responsive-web-design/applied-visual-design/animate-elements-at-variable-rates"}}

